import { Component, OnInit, OnDestroy } from '@angular/core';
import {
  properties,
  propertiesMobile,
} from '../../assets/js/particles-properties';

declare var particlesJS: any;
declare var ScrollMagic: any;
declare var TimelineMax: any;
declare var Power4: any;

@Component({
  selector: 'app-work',
  templateUrl: './work.component.html',
  styleUrls: ['./work.component.css'],
})
export class WorkComponent implements OnInit, OnDestroy {
  portfolio = [
    {
      id: 'intro',
      title: 'Intro',
      description: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit...',
      image: 'intro-image.jpg',
    },
    {
      id: 'first',
      title: 'Diagnostic ECG',
      description: 'I contribute to the team, at GE Healthcare...',
      image: 'diagnostic-ecg-image.jpg',
    },
    // Add more works here as needed
  ];
    activeIndex = 0;
  
    constructor() {}
    scrollToWork(index: number): void {
      this.activeIndex = index;
      const element = document.getElementById(this.portfolio[index].id);
      if (element) {
        element.scrollIntoView({ behavior: 'smooth' });
      }
    }
  
  

  ngOnInit(): void {
    console.log('reloadss');
    let body = document.querySelector('body');
    let nav = document.querySelector('nav');
    let addParticle = document.querySelector('.addParticles');
  
    setTimeout(() => {
      addParticle.classList.add('fadeBar');
    }, 5000);
  
    if (!body.classList.contains('container-work')) {
      body.classList.add('container-work');
      nav.classList.add('work-nav');
    }
  
    var mq = window.matchMedia('(max-width: 1024px)');
    let props: any;
    if (mq.matches) {
      props = propertiesMobile;
    } else {
      props = properties;
    }
  
    particlesJS('particles-js', props, function () {
      console.log('callback');
    });
  
    let wh = window.innerHeight;
    let projectSection = document.querySelectorAll('.proj-section');
    let timeline = new TimelineMax();
    let controller = new ScrollMagic.Controller();
  
    projectSection.forEach((projs, index) => {
      let circle = projs.querySelector('.circle');
      let title = projs.querySelector('.title-proj');
  
      timeline
        .fromTo(
          circle,
          1,
          { opacity: 0, scale: 0.5 },
          { opacity: 1, scale: 1, ease: Power4.easeInOut }
        )
        .fromTo(
          title,
          1,
          { opacity: 0, y: -20 },
          { opacity: 1, y: 0, ease: Power4.easeInOut },
          '-=0.8'
        );
  
      new ScrollMagic.Scene({
        triggerElement: projs,
        triggerHook: 0.7,
        duration: '100%'
      })
        .setTween(timeline)
        .addTo(controller)
        .on('enter', (e) => {
          e.target.triggerElement().classList.add('active');
        })
        .on('leave', (e) => {
          e.target.triggerElement().classList.remove('active');
        });
  
      timeline = new TimelineMax();
    });
  }
  
  
  sectionChange(projNum) {
    document.getElementById(projNum).scrollIntoView(true);
  }

  ngOnDestroy() {
    let nav = document.querySelector('nav');
    let body = document.querySelector('body');
    body.classList.remove('container-work');
    nav.classList.remove('work-nav');
    window.scrollTo(0, 0);
  }
}
